<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small"> 重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="联系人姓名：">
            <el-input @input="change($event)" style="width: 203px" v-model.trim="searchList.sendUserName" clearable placeholder="联系人姓名"></el-input>
          </el-form-item>
          <el-form-item label="联系电话：">
            <el-input @input="change($event)" style="width: 203px" v-model.trim="searchList.telephone" clearable placeholder="联系电话"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="newShow = true">新增物流地址</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table v-loading="dataLoading" :data="list" style="width: 100%">
          <el-table-column label="联系人姓名" prop="receiverName"></el-table-column>
          <el-table-column label="联系电话" prop="receiverPhone"></el-table-column>
          <el-table-column label="所在地区" prop="area"></el-table-column>
          <el-table-column label="详细地址" prop="receiverAddress"></el-table-column>
          <el-table-column label="默认">
            <template slot-scope="scope">
              <div v-if="scope.row.isDefault == 1">是</div>
              <div v-if="scope.row.isDefault == 0">否</div>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" v-if="scope.row.isDefault == 0" @click="setDefault(scope.row.id)">设为默认</el-button>
              <el-button type="text" size="small" @click="showEdit(scope.row.id)">编辑</el-button>
              <el-button v-if="scope.row.isDefault == 0" class="dangerFont" type="text" size="small" @click="deleteItem(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <!-- 分页 -->
      <div class="pageDown">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </div>
    <!-- 新增物流 -->
    <el-dialog title="新增物流地址" :visible.sync="newShow" width="70%">
      <el-form ref="newForm" :model="newForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="联系人姓名：" required>
          <el-input style="max-width:400px;" v-model.trim="newForm.receiverName" maxlength="10" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="联系电话：" required>
          <el-input style="max-width:400px;" v-model.trim="newForm.receiverPhone" maxlength="11" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="自提点地址：" required>
          <div class="flex-row-start">
            <el-form-item style="margin-right:10px;">
              <el-select @change="address1" value-key="code" v-model="newForm.province" placeholder="请选择省">
                <el-option v-for="item in addressList1" :key="item.code" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item style="margin-right:10px;">
              <el-select @change="address2" value-key="code" v-model="newForm.city" placeholder="请选择市">
                <el-option v-for="item in addressList2" :key="item.code" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-select v-model="newForm.district" value-key="code" placeholder="请选择区">
                <el-option v-for="item in addressList3" :key="item.code" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-form-item>
        <el-form-item label="详细地址：" required>
          <el-input style="max-width:400px;" v-model.trim="newForm.receiverAddress" maxlength="50" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="设为默认">
          <el-switch v-model="newForm.isDefault" inactive-value="0" active-value="1"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button :loading="newSubLoading" type="primary" @click="newSub('newForm')">立即创建</el-button>
          <el-button @click="newShow = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 编辑物流 -->
    <el-dialog title="编辑物流地址" :visible.sync="editShow" width="70%">
      <el-form :model="editForm" ref="newExpForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="联系人姓名：" required>
          <el-input style="max-width:400px;" v-model.trim="editForm.receiverName" maxlength="10" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="联系电话：" required>
          <el-input style="max-width:400px;" v-model.trim="editForm.receiverPhone" maxlength="11" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="自提点地址：" required>
          <div class="flex-row-start">
            <el-form-item style="margin-right:10px;">
              <el-select @change="address1" value-key="code" v-model="editForm.province" placeholder="请选择省">
                <el-option v-for="item in addressList1" :key="item.code" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item style="margin-right:10px;">
              <el-select @change="address2" value-key="code" v-model="editForm.city" placeholder="请选择市">
                <el-option v-for="item in addressList2" :key="item.code" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-select v-model="editForm.district" value-key="code" placeholder="请选择区" @change="selectDisposeCode">
                <el-option v-for="item in addressList3" :key="item.code" :label="item.name" :value="item"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-form-item>
        <el-form-item label="详细地址：" required>
          <el-input style="max-width:400px;" v-model.trim="editForm.receiverAddress" maxlength="50" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="设为默认">
          <el-switch v-model="editForm.isDefault" inactive-value="0" active-value="1"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button :loading="editSubLoading" type="primary" @click="editSub">确定</el-button>
          <el-button @click="editShow = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getAddressLevel } from '@/api/shop/createShop'
import { 
  getReturnPage,
  insertShopReturn,
  setDefaultReturn,
  deleteDefaultReturn,
  shopReturnDetail,
  updateShopReturn
} from '@/api/logistics/logisticsAddress'
export default {
  data(){
    return{
      searchList:{
        pageNum:1,
        pageSize:5
      },              // 搜索栏数据
      list:[],                    // 列表数据
      listTotal:0,
      dataLoading:false,
      newForm:{},       // 新增数据
      newShow:false,
      newSubLoading:false,
      addressList1:[],              // 创建 - 下拉 - 地址1级
      addressList2:[],              // 创建 - 下拉 - 地址2级
      addressList3:[],              // 创建 - 下拉 - 地址3级
      editForm:{},
      editShow:false,
      editSubLoading:false,
      editaddressList1:[],              // 创建 - 下拉 - 地址1级
      editaddressList2:[],              // 创建 - 下拉 - 地址2级
      editaddressList3:[],              // 创建 - 下拉 - 地址3级
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData();
    this.getAddressInput(1,'')
  },
  methods:{
    selectDisposeCode(){
      this.$forceUpdate();
    },
    // 
    showEdit(id){
      this.editShow = true;
      shopReturnDetail(id).then(res=>{
        console.log('数据详情',res)
        this.editForm = res.data;
        this.editForm.isDefault = '' + this.editForm.isDefault
        this.editForm.province = {
          name:res.data.receiverProvinceName,
          code:res.data.receiverProvinceCode
        }
        this.getAddressInput(2,res.data.receiverProvinceCode)
        this.editForm.city = {
          name:res.data.receiverCityName,
          code:res.data.receiverCityCode
        }
        this.getAddressInput(3,res.data.receiverCityCode)
        this.editForm.district = {
          name:res.data.receiverDistrictName,
          code:res.data.receiverDistrictCode
        }
      })
    },
    editSub(){
      console.log('editForm',this.editForm)
      if(!this.editForm.receiverName){
        this.$message.error('请填写发货人姓名');
        return;
      }
      if(!this.editForm.receiverPhone){
        this.$message.error('请填写发货人联系电话');
        return;
      }
      if(!this.editForm.province || !this.editForm.city || !this.editForm.district){
        this.$message.error('请选择省市区');
        return;
      }
      this.editForm.receiverProvinceName = this.editForm.province.name
      this.editForm.receiverProvinceCode = this.editForm.province.code
      this.editForm.receiverCityName = this.editForm.city.name
      this.editForm.receiverCityCode = this.editForm.city.code
      this.editForm.receiverDistrictName = this.editForm.district.name
      this.editForm.receiverDistrictCode = this.editForm.district.code
      if(!this.editForm.receiverAddress){
        this.$message.error('请填写详细地址');
        return;
      }
      this.editForm.shopId = this.shop.shopId
      this.editSubLoading = true;
      updateShopReturn(this.editForm).then(res=>{
        console.log('编辑收货地址',res)
        if(res.code == 200){
          this.$message({
            message: '编辑成功',
            type: 'success'
          });
          this.getData()
          this.clearEditForm();
          this.editShow = false;
          setTimeout(()=>{
            this.editSubLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
          this.editSubLoading = false;
        }
        
      })
    },
    // 设置默认
    setDefault(id){
      setDefaultReturn({id:id,shopId:this.shop.shopId}).then(res=>{
        console.log('设置默认',res)
        if(res.code == 200){
          this.$message({
            message: '设置成功',
            type: 'success'
          });
          this.getData()
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 删除
    deleteItem(id){
      this.$confirm('此操作将删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteDefaultReturn({id:id}).then(res=>{
          console.log('删除',res)
          if(res.code == 200){
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 创建店铺 - 下拉选择地址
    getAddressInput(id1,id2){
      getAddressLevel(id1,id2).then(res=>{
        console.log(`${id1}级下拉获取下拉框地址`,res)
        if(id1 == 1){
          this.addressList1 = res.data
          return;
        }
        if(id1 == 2){
          this.addressList2 = res.data
          return;
        }
        if(id1 == 3){
          this.addressList3 = res.data
          return;
        }
      })
    },
    // 数据列表
    getData(){
      this.dataLoading = true;
      console.log('请求列表的数据',this.searchList)
      getReturnPage(this.searchList).then(res=>{
        console.log('获取数据列表',res)
        this.list = res.data.list;
        this.listTotal = res.data.total
        this.dataLoading = false;
      })
    },
    newSub(){
      console.log('newForm',this.newForm)
      if(!this.newForm.receiverName){
        this.$message.error('请填写发货人姓名');
        return;
      }
      if(!this.newForm.receiverPhone){
        this.$message.error('请填写发货人联系电话');
        return;
      }
      if(!this.newForm.province || !this.newForm.city || !this.newForm.district){
        this.$message.error('请选择省市区');
        return;
      }
      this.newForm.receiverProvinceName = this.newForm.province.name
      this.newForm.receiverProvinceCode = this.newForm.province.code
      this.newForm.receiverCityName = this.newForm.city.name
      this.newForm.receiverCityCode = this.newForm.city.code
      this.newForm.receiverDistrictName = this.newForm.district.name
      this.newForm.receiverDistrictCode = this.newForm.district.code
      if(!this.newForm.receiverAddress){
        this.$message.error('请填写详细地址');
        return;
      }
      this.newForm.shopId = this.shop.shopId
      this.newSubLoading = true;
      insertShopReturn(this.newForm).then(res=>{
        console.log('新增收货地址',res)
        if(res.code == 200){
          this.$message({
            message: '创建成功',
            type: 'success'
          });
          this.getData()
          this.clearNewForm();
          this.newShow = false;
          setTimeout(()=>{
            this.newSubLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
          this.newSubLoading = false;
        }
        
      })
    },
    clearNewForm(){
      this.newForm.receiverName = ''
      this.newForm.receiverPhone = ''
      this.newForm.province = {}
      this.newForm.city = {}
      this.addressList2 = []
      this.newForm.district = {}
      this.addressList3 = []
      this.newForm.receiverAddress = ''
      this.newForm.isDefault = 0
    },
    clearEditForm(){
      this.editForm.receiverName = ''
      this.editForm.receiverPhone = ''
      this.editForm.province = {}
      this.editForm.city = {}
      this.addressList2 = []
      this.editForm.district = {}
      this.addressList3 = []
      this.editForm.receiverAddress = ''
      this.editForm.isDefault = 0
    },
    // 创建店铺 - 下拉地址 2级
    address1(val){
      console.log('执行了1',val)
      this.addressList2 = [];
      this.addressList3 = [];
      if(this.newShow){
        if(this.newForm.city) this.newForm.city = {};
        if(this.newForm.district) this.newForm.district = {};
      }
      if(this.editShow){
        if(this.editForm.city) this.editForm.city = {};
        if(this.editForm.district) this.editForm.district = {};
      }
      this.getAddressInput(2,val.code);
    },
    // 创建店铺 - 下拉地址 3级
    address2(val){
      console.log('执行了2',val)
      this.addressList3 = [];
      if(this.newShow){
        if(this.newForm.district) this.newForm.district = {};
      }
      if(this.editShow){
        if(this.editForm.district) this.editForm.district = {};
      }
      this.getAddressInput(3,val.code);
    },
    // 搜索栏重置
    reset(){
      this.searchList.sendUserName = '';
      this.searchList.telephone = '';
      this.getData()
    },
    handleSizeChange(val) {
      this.searchList.pageSize = val;
      this.getData()
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.searchList.pageNum = val;
      this.getData()
      console.log(`当前页: ${val}`);
    },
    change(e){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .pageDown{
    padding: 20px 0;
  }
</style>