import request from '@/utils/request'
// 发货地址分页查询
export function getReturnPage(data) {
  return request({
    url: `/sys/shop/return/getReturnPage`,
    method: 'post',
    data: data
  })
}
// 新增收货地址
export function insertShopReturn(data) {
  return request({
    url: `/sys/shop/return/insertShopReturn`,
    method: 'post',
    data: data
  })
}
// 设置默认发货地址
export function setDefaultReturn(data) {
  return request({
    url: `/sys/shop/return/setDefaultReturn`,
    method: 'post',
    data
  })
}
// 删除发货地址
export function deleteDefaultReturn(data) {
  return request({
    url: `/sys/shop/return/deleteDefaultReturn`,
    method: 'post',
    data: data
  })
}
// 获取地址详情
export function shopReturnDetail(id) {
  return request({
    url: `/sys/shop/return/shopReturnDetail?id=${id}`,
    method: 'get',
  })
}
// 删除发货地址
export function updateShopReturn(data) {
  return request({
    url: `/sys/shop/return/updateShopReturn`,
    method: 'post',
    data: data
  })
}